import { Field, ObjectType } from "@nestjs/graphql";
import { Column, Entity, ManyToMany, ManyToOne } from "typeorm";
import { BaseEntityColumn } from "../base.entity";
import { PaymentGatewayKey } from "./payment-gateway-key.entity";
import { PaymentGateway } from "./payment-gateway.entity";
import { PaymentPlan } from "./payment-plan.entity";

@ObjectType()
@Entity()
export class PaymentGatewayPlan extends BaseEntityColumn {

  @Field(() => Number)
  @Column()
  percentage: number;

  @Field(() => PaymentPlan)
  @ManyToOne(() => PaymentPlan, (paymentPlan) => paymentPlan.paymentGatewayPlans)
  paymentPlan: PaymentPlan;

  @Field(() => PaymentGateway)
  @ManyToOne(() => PaymentGateway, (paymentGateway) => paymentGateway.paymentGatewayPlans, { orphanedRowAction: "soft-delete" })
  paymentGateway: PaymentGateway;

  @Field(() => [PaymentGatewayKey])
  @ManyToMany(() => PaymentGatewayKey, (paymentGatewayKey) => paymentGatewayKey.paymentGatewayPlans, { orphanedRowAction: "soft-delete" })
  paymentGatewayKeys: PaymentGatewayKey[];

}
